@charset "UTF-8";
:root {
  --clip: left center;
  --size: 0;
  --background-thumb: #2b3769;
  --scrollbar-thumb-bgcolor: #757575;
  --shadow-size: 0px 0px 10px 1px rgba(0, 0, 0, 0.4);
  --stripe-width: 10px;
  --nav-marker-border: #3b8686;
  --sidebar-width: 28.5rem;
  --duration: 0.3s;
  --button-size: 4rem;
  --mob-expanded-width: 320px;
  --mob-collapsed-width: 4rem;
}

.masked-input {
  color: transparent; /* Скрыть текст, пока маска не активна */
}

.masked-input::placeholder {
  color: black; /* Цвет плейсхолдера */
}

.gradient-preloader {
  background: linear-gradient(90deg, #2b3769 -23.739%, #93a9ff 96.778%);
}

@media (min-width: 1440.99px) {
  .underline:hover {
    opacity: 1;
  }
  .underline:hover::before {
    width: 47%;
  }
  .underline:hover::after {
    width: 47%;
  }
  .underline::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #000;
    opacity: 0.8;
    transition: width 0.3s ease-in-out;
  }
  .underline-w:hover {
    opacity: 1;
  }
  .underline-w:hover::before {
    width: 47%;
  }
  .underline-w:hover::after {
    width: 47%;
  }
  .underline-w::before {
    content: "";
    position: absolute;
    right: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
  .underline-w::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: 0;
    width: 0;
    height: 1px;
    background-color: #fff;
    opacity: 0.8;
    transition: width 0.4s ease-in-out;
  }
}
.project-page-section__title {
  text-align: center;
  margin-bottom: 40px;
}
.project-page-section__text {
  margin: 0 auto;
  font-family: "averta-regular", sans-serif;
  line-height: 125%; /* 20px/16 */
  max-width: 975px;
  text-align: center;
  margin-bottom: 7rem;
}
.project-page-section__text span {
  white-space: nowrap;
}
@media screen and (max-width: 690px) {
  .project-page-section__text {
    margin-bottom: 30px;
  }
}
.project-page-section__content {
  position: relative;
  z-index: 2;
  background: #fff;
}
@media (min-width: 960.99px) {
  .project-page-section__content {
    border-radius: 30px;
  }
}
@media screen and (max-width: 1140px) {
  .project-page-section__content {
    border-top-right-radius: 0;
    border-top-left-radius: 0;
  }
}
.project-page-section__footer {
  display: flex;
  justify-content: center;
}
.project-page-section__button {
  background: #2b3769;
  border: 1px solid transparent;
  color: #fff;
}
.project-page-section__button:hover {
  border: 1px solid #2b3769;
}
.project-page-section__button:hover .icon {
  transform: rotate(-90deg);
  background: #2b3769;
}
.project-page-section__button:hover .icon .icon-arrow-down::before {
  color: #fff;
  font-weight: 500;
}
.project-page-section__button:hover span {
  color: #000;
}
.project-page-section__button:hover::before {
  background: #fff;
}
.project-page-section__icon .icon-arrow-down::before {
  color: #2b3769;
  font-weight: 600;
}
.project-page-section__icon.icon {
  background: #fff;
}

.tab-content {
  position: relative;
  height: 100%;
}
.tab-content__items {
  display: none;
  margin-bottom: 40px;
  grid-template-columns: repeat(3, 1fr);
  gap: 40px;
}
@media screen and (max-width: 1440px) {
  .tab-content__items {
    grid-template-columns: repeat(2, 1fr);
    gap: 30px;
  }
}
@media screen and (max-width: 690px) {
  .tab-content__items {
    grid-template-columns: repeat(1, 1fr);
  }
}
.tab-content__items span {
  display: inline;
}
.tab-content__items.active {
  display: grid !important;
}
.tab-content__column {
  cursor: pointer;
  position: relative;
  display: flex;
  flex-direction: column;
}
@media (min-width: 960.99px) {
  .tab-content__column:hover .tab-content__text {
    opacity: 1;
    scale: 1;
  }
}
.tab-content__image {
  cursor: pointer;
  width: 100%;
  overflow: hidden;
  border-radius: 30px;
  overflow: hidden;
  margin-bottom: 5px;
}
.tab-content__image img {
  width: 100%;
  height: 100%;
  object-fit: contain;
}
@media (min-width: 768.98px) {
  .tab-content__image {
    pointer-events: none;
  }
}
@media screen and (max-width: 768px) {
  .tab-content__image {
    margin-bottom: 5px;
    border-radius: 15px;
  }
}
.tab-content__footer {
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  gap: 10px;
  justify-content: space-between;
}
@media (min-width: 1440.98px) and (max-width: 1740px) {
  .tab-content__footer {
    flex-direction: column;
    gap: 20px;
  }
}
@media screen and (max-width: 1164px) {
  .tab-content__footer {
    flex-direction: column;
    gap: 20px;
  }
}
.tab-content__text {
  pointer-events: none;
  color: #fff;
  line-height: 125%; /* 20px/16 */
  text-align: left;
  flex: 1 1 auto;
  display: none;
  border-radius: 30px;
}
@media screen and (max-width: 1164px) {
  .tab-content__text {
    max-width: 100%;
    width: 100%;
  }
}
@media (min-width: 960.99px) {
  .tab-content__text {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    padding: 30px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: flex-end;
    background: rgba(60, 59, 81, 0.8078431373);
    scale: 0;
    opacity: 0;
    transition: opacity 0.4s, scale 0.4s ease 0s;
  }
  .tab-content__text span {
    height: 100%;
    display: flex;
    align-items: center;
  }
}
@media screen and (max-width: 960px) {
  .tab-content__text {
    display: block;
    position: relative;
    color: #000;
  }
}
.tab-content__button {
  right: 0;
  bottom: 0;
  background: #2b3769;
  border: 1px solid transparent;
}
.tab-content__button span {
  color: #fff;
}
.tab-content__button:hover .icon {
  transform: rotate(-45deg);
  background: #2b3769;
}
.tab-content__button:hover ._icon::before {
  color: #fff;
  font-weight: 400;
}
.tab-content__button:hover span {
  color: #000;
}
.tab-content__button:hover::before {
  background: #fff;
  color: #fff;
}
.tab-content__button .icon {
  background: #fff;
}
.tab-content__button ._icon {
  border-radius: 50%;
  color: #2b3769;
}
.tab-content__button ._icon::before {
  font-weight: 600;
}
@media screen and (max-width: 960px) {
  .tab-content__button {
    width: 268px;
  }
}
.tab-content__icon {
  flex: 0 0 4.8rem;
  pointer-events: all;
  width: 4.8rem;
  height: 4.8rem;
  border-radius: 50%;
  background: #fff;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-in-out;
  overflow: hidden;
  display: grid;
  grid-template-columns: 1fr;
}
.tab-content__icon:hover .icon-arrow-right {
  transform: translateY(-100%);
}
.tab-content__icon:hover .icon-group {
  transform: translateY(-100%);
}
@media screen and (max-width: 960px) {
  .tab-content__icon {
    display: none;
  }
}
.tab-content__icon ._icon {
  transition: all 0.5s ease 0s;
  height: 4.8rem;
}
.tab-content__icon ._icon::before {
  color: #2b3769;
  font-weight: 700;
}
.tab-content__icon .icon-group::before {
  font-size: 14px;
}